export default {
	props: {
		list: {
			type: Array
		},
		scrollHeight: {
			type: Number
		}
	},
	data() {
		return {
			scrollTop: 0
		}
	},
	onLoad() {
		
	},
	methods: {
		toAtlas: function(item) {
			console.log(123)
			this.$emit('toAtlas',item)
		},
		lower: function() {
			this.$emit('lower')
		},
		scroll:  function(e) {
			console.log(e.detail.scrollTop)
			if(this.scrollTop < e.detail.scrollTop) {
				this.$emit('isScroll',false)
			} else {
				this.$emit('isScroll',true)
			}
			this.scrollTop = e.detail.scrollTop
		}
	}
}
